<template>
    <div class="content">
        <el-row :class="{'row-bg': true}" type="flex">
            <el-col>
                <el-select v-model="filter" placeholder="Filtrar">
                    <el-option v-for="f in filters" :label="f.label" :value="f.value"></el-option>
                </el-select>
            </el-col>
            <el-col>
                Mude entre:<br/>
                Refeições <el-switch v-model="opcao" on-text="" off-text="" :class="{'light-silver': opcao, 'silver': !opcao}" ></el-switch> Lanches
            </el-col>
            <el-col>
                <el-table :data="tableData" class="tabela" max-height="400" border>
                    <el-table-column type="expand">
                        <template scope="props">
                            <div v-for="el in props.row.pedidos.list" class="prods">
                                <span><b>Produto:</b> {{el.produto}}</span>
                                <span><b>QTDE:</b> {{el.qtde}}</span>
                            </div>
                            <div class="price">
                                <p class="green"><b>Preço Final:</b> R$ {{props.row.preco_final}}</p>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column prop="turma" label="Ano"></el-table-column>
                    <el-table-column prop="nome" label="Nome" min-width="100"></el-table-column>
                    <el-table-column prop="cat" label="Categoria" min-width="100">
                        <template scope="scope">
                            <el-tag>{{scope.row.cat}}</el-tag>
                        </template>
                    </el-table-column>
                    <!--el-table-column prop=-->
                </el-table>
            </el-col>
        </el-row>
    </div>
</template>
<script>

    import axios from 'axios';
    export default {
        data() {
            return {
                filters: [{
                    label: 'Ano',
                    value: 'ano'
                }, {
                    label: 'Nome',
                    value: 'nome'
                }, {
                    label: 'Horário',
                    value: 'horario'
                }],
                filter: '',
                tableData: [],
                opcao: false,
                pedidosLanches: [],
                pedidosRefeicao: []
            }
        },
        watch: {
            opcao() {
                this.request();
            },
            pedidosLanches() {
                let table = this.pedidosLanches;
                //console.log(table);
                console.log(Object.keys(table).length);
                for (let n in table) {
                    console.log(table[n]);
                    table[n].cat = 'Lanche';
                    let ped = table[n].pedidos;
                    //console.log(typeof ped+ ' ' + ped);
                    let pedJSON = JSON.parse(ped);
                    //console.log(typeof ped, '1');
                    //console.log(pedJSON);
                    table[n].pedidos = pedJSON;
                    if (pedJSON) {
                        for (let l in pedJSON) {
                            let lista = pedJSON[l];
                            //console.log(lista);
                            lista.forEach(function(el, index, arr) {
                               // console.log(el);
                            });
                        }
                    }
                    this.tableData.unshift(table[n]);

                    /*console.log('json ', ped['list']);
                    for (let l in pedJSON) {
                        console.log(ped[l]);
                    }*/
                }
                console.log(this.tableData);
            }
        },
        mounted() {
            this.request();
        },
        methods: {
            lanches() {
                for (let pedido in this.pedidosLanches) {
                    //console.log(this.pedidosLanches[pedido]);
                }
                //this.tableData = this.pedidosLanches;
            },
            request() {
                const lanches = 'api/cantina/pedidos';
                const refeicao = 'api/cantina/pedidos/refeicoes';
                let op = '';
                (this.opcao) ? op = lanches : op = refeicao;
                axios.post(op, {nivel: window.sessionStorage.getItem('nivel')})
                    .then(response => {
                        //console.log('response');
                        //console.log(response);
                        (this.opcao) ? this.pedidosLanches = response.data : this.pedidosRefeicao = response.data;
                        
                    }).catch(error => {
                        console.log('error');
                        console.log(error);
                    });
            }
        }
    }
</script>
<style scoped>

.content {
        height: 100vh;
        display: flex;
        justify-content: center;
    }

    .row-bg {
        flex-direction: column;
        margin-top: 3vh;
        align-items: center;
    }

    .tabela {
        width: 100%;
    }

    .prods {
        display: flex;
        justify-content: space-between;
    }
    .price {
        margin-top: 5px;
    }
</style>


