<template>
    <div>
    <el-dialog title="Como funciona" v-model="intro">
            <div class="info">
                <p>Como fazer um pedido:</p>
                <p>1. Escolher o produto e a quantidade.</p>
                <p>2. (Opcional) Escolher outro produto e a quantidade. </p>
                <p>3. Visualização dos produtos e preço total.</p>
                <p>Clique em finalizar, ou utilize a opção de voltar para alterar algum produto.</p>
            </div>
        </el-dialog>
        <el-col class="intro">
            <el-row type="flex" class="title">
                <el-col>Introdução</el-col>
            </el-row>
            <el-row class="content">
                <el-col>
                    <el-button @click="intro = true" type="text">Como funciona</el-button>
                </el-col>
                <el-col>
                    <el-date-picker v-model="date" type="date" placeholder="Escolha um dia" format="dd/MM/yyyy" :picker-options="pickerOp"></el-date-picker>
                </el-col>
                <!--el-col>Para realizar o pedido da refeição para seu filho, é simples:</el-col>
                <el-col>1. Você informa a quantidade e digita os detalhes do prato.</el-col>
                <el-col>2. Você pode selecionar outro tipo de produto e a quantidade.</el-col>
                <el-col>3. Você visualiza tudo o que selecionou com o preço total.</el-col>
                <el-col>Clique em finalizar, ou utilize a opção de voltar para alterar algum produto.</el-col-->
                <el-col>
                    <el-radio-group v-model="filho" size="small">
                        <el-radio class="radioB" v-for="f in listaFilhos" :label="f.id">{{f.nome}}</el-radio>
                    </el-radio-group>
                    <!--el-checkbox-group v-model="filhosCheck">
                        <el-checkbox v-for="filho in listaFilhos" :label="filho">{{filho}}</el-checkbox>
                    </el-checkbox-group-->
                </el-col>
            </el-row>
        </el-col>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                listaFilhos: [{
                    id: 1,
                    nome: 'filho1'
                }, {
                    id: 2,
                    nome: 'filho2'
                }, {
                    id: 3,
                    nome: 'filho3'
                }],
                filho: '',
                intro: false,
                pickerOp: {
                    shortcuts: [{
                        text: 'Hoje',
                        onClick(picker) {
                            picker.$emit('pick', new Date());
                        }
                    }, {
                        text: 'Amanhã',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() + 3600 * 1000 * 24);
                            picker.$emit('pick', date);
                        }
                    }]
                },
                date: ''
            }
        },
        mounted() {
            const filhos = this.$store.getters.getFilhos;
            console.log(filhos);
            if (!filhos.hasOwnProperty('length')) {
                this.listaFilhos = filhos;
            }
        },
        watch: {
            filho() {
                this.$store.dispatch('changeFilhosRefeicao', this.filho);
            },
            date() {
                this.$store.dispatch('addDateRefeicao', this.date);
            }
        }
    }
</script>
<style scoped>
    .title {
        margin-bottom: 1.5rem;
        text-align: center;
    }
    .intro {
        align-items: center;
        font-size: 1.25rem;
       
    }
    .info {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
    }
    .info p {
        margin-bottom: 0.75rem;
    }
    .radioB {
        display: flex;
        margin-left: 0 !important;
        margin-top: 10px;
    }

    .content {
        display: flex;
        flex-direction: column;
    }

    .content > * {
        margin-top: 15px;
    }
</style>


