<template>
    <div class="bg">
        <el-row type="flex" class="row-bg" align="middle">
            <el-col :md="{offset: 7, span: 10}" :sm="{offset: 6, span: 12}" :xs="{offset: 2, span: 20}">
                <el-card class="card card-login">
                    <el-tooltip class="item" content="Informe um email válido!" placement="top-start">
                        <el-input class="inputs" id="emailInput" placeholder="Usuário" :class="{warning: !emailV, success: emailV}" type="email" v-model="email"></el-input>
                    </el-tooltip>
                    <el-input class="inputs" id="passwordInput" v-model="password" :class="{warning: !passwordV, success: passwordV}" type="password" placeholder="Senha" @enter="request()"></el-input>
                    <a @click="signUpVisible = true">Fazer registro</a>
                    <div class="buttons">
                        <el-button size="small" type="danger">Cancelar</el-button>
                        <el-button size="large" type="success" @click="request()" >Entrar</el-button>
                    </div>
                </el-card>
            </el-col>
        </el-row>
        <el-dialog title="Registrar usuário" v-model="signUpVisible">
            <el-form :model="signUpForm">
                <el-form-item label="Email">
                    <el-input v-model="signUpForm.email"></el-input>
                </el-form-item>
                <el-form-item label="Senha">
                    <el-input type="password" v-model="signUpForm.password"></el-input>
                </el-form-item>
                <el-form-item label="Nivel">
                    Responsável? <el-switch v-model="signUpForm.nivel" on-text="Sim" off-text="Não"></el-switch>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="signUpVisible = false">Cancelar</el-button>
                <el-button @click="signup" type="primary">Registrar</el-button>
            </span>
        </el-dialog>

    </div>
</template>
<script>

    import axios from 'axios';
    export default {
        data() {
            return {
                email: '',
                password: '',
                emailV: '',
                passwordV : '',
                signUpVisible: false,
                signUpForm: {
                    email: '',
                    password: '',
                    nivel: false
                }
            }
        },
        watch: {
            email(newEmail) {
                const atPos = newEmail.indexOf("@");
                const dotPos = newEmail.lastIndexOf(".");
                if (atPos < 1 || dotPos < atPos+2 || dotPos+2 >= newEmail.length) {
                    this.emailV = false;
                } else {
                    this.emailV = true;
                }
            },
            password(newPassword) {
                (this.password.length) ? (this.passwordV = true) : (this.passwordV = false);  
            }
        },
        methods: {
            signup() {
                const nv = (this.signUpForm.nivel) ? 2 : 3;
                axios.post('api/register',
                    {
                        email: this.signUpForm.email,
                        password: this.signUpForm.password,
                        nivel: nv,
                        nome: ('Pessoa ' + Math.floor((Math.random() * 100) + 1))
                    }).then(response => {
                        console.log('response');
                        console.log(response);
                    }).catch(error => {
                        console.log('error');
                        console.log(error);
                    });
            },
            checkEntry() {
                if(this.emailV) {
                    if(this.password.length) {
                        console.log("EMAIL E SENHA VÁLIDOS");
                    } else {
                        console.log("SENHA EM BRANCO");  
                    }
                } else {
                    console.log("EMAIL INVÁLIDO");   
                }
            },
            accessGranted(cod, user, filhos) {
                window.sessionStorage.setItem('nivel', cod);
                window.sessionStorage.setItem('user', user);
                //console.log(filhos);
                if (!filhos) {
                    console.log('filhos está vazio!');
                    window.sessionStorage.setItem('filhos', JSON.stringify({filhos: ''}));
                } else {
                    window.sessionStorage.setItem('filhos', JSON.stringify(filhos));
                    this.$store.dispatch('addFilhos', JSON.parse(window.sessionStorage.getItem('filhos')));
                }
                
                //this.$store.dispatch('addFilhos', filhos);
                this.$router.push('home');

            },
            serverError() {

            },
            invalidUser() {
                this.$message.error('Login ou senha inválido!');
            },
            logon(resp, user, filhos) {
                 // -1 ERRO NO SERVER; 0 ERRO DE LOGIN; 2 ACESSO COMO PAI; 3 ACESSO COMO CANTINA
                switch(resp) {
                    case -1: 
                        this.serverError();
                        break;
                    case 0:
                        this.invalidUser();
                        break;
                    default:
                       this.accessGranted(resp, user, filhos);

                }
            },
            request() {
                const email = this.email;
                const password = this.password;
                const func = this.logon;
                axios.post("api/login", {email , password})
                    .then(response => {
                        //console.log(response);
                        func(response.data.nivel, response.data.user, response.data.filhos);
                    })
                     .catch(error => {
                        console.log("ERRO: ");
                        console.log(error);
                        func(-1);
                     });
                     
            }
        },
        mounted() {
            window.sessionStorage.setItem('nivel', 0);
        }
    }
</script>
<style scoped>

    .warning {
        color: #F7BA2A;
    }
    .row-bg {
        height: 100vh;
        background-color: #1f1f1f; /*#cccccc */
    }

    .card-login {
        background-color:  #3f3f3f; /*#404555; /*#6a6a6a; /*#afaacc */
        border: none;
    }
    .inputs {
        margin-bottom: 10px;
    }

    .buttons {
        display: flex;
        justify-content: space-between;
    }
</style>


