<template>
    <!-- DENTRO DE REFEIÇÃO -->
    <div class="step2">
        <el-col>
            <el-input-number :class="{inputs: true, warning: elQtde}" v-model="qtde" :min="0" :max="10" ></el-input-number>
        </el-col>
        <el-col>
            <el-input :class="{inputs: true, textarea: true}" type="textarea" :rows="2" placeholder="Digite informações adicionais" v-model="info"></el-input>
        </el-col>
        <el-col>
            <el-button type="success" @click="add">Adicionar</el-button>
        </el-col>
        <el-col>
            <el-table height="150" border :data="tableData" @row-click="apagarItem">
                <el-table-column prop="produto" label="Produto" width="100" align="left">
                </el-table-column>
                <el-table-column prop="qtde" label="Qtde" width="70" align="right">
                </el-table-column>
                <el-table-column prop="unit" label="PU" width="70" align="right">
                </el-table-column>
                <el-table-column prop="info" label="Info" width="70" align="left">
                </el-table-column>
            </el-table>
        </el-col>
    </div>
</template>
<script>

    export default {
        //props: ['refeicao'],
        data() {
            return {
                tableData: [],
                qtde: 0,
                elQtde: false,
                info: '',
                refeicao: {
                    id: '01',
                    name: 'Refeição comum',
                    price: '10.00'
                }
            }
        },
        methods: {
            add() {
                if (this.qtde) {
                        let temInfo;
                        (this.info.length > 0) ? temInfo = 'Sim' : temInfo = 'Não';
                        let pedido = {
                            id: this.refeicao.id,
                            produto: this.refeicao.name, 
                            qtde: this.qtde, 
                            unit: this.refeicao.price,
                            info: temInfo
                        };
                        this.tableData.unshift(pedido);
                        let pedido2 = Object.assign({}, pedido);
                        pedido2.info = this.info;
                        this.$store.dispatch('addListaRefeicao', pedido2);
                        console.log(this.$store.getters.getListaRefeicao);
                        this.elQtde = false;
                    } else {
                        this.$notify({
                            title: 'Aviso',
                            message: 'Quantidade inválida!',
                            type: 'warning'
                        });
                        this.elQtde = true;
                    }
            },
            apagarItem(row, event, column) {
                this.$confirm('Deseja apagar este lanche?', 'Aviso', {
                    confirmButtonText: 'Sim',
                    cancelButtonText: 'Não',
                    type: 'warning'
                }).then(() => {
                    this.$message({
                        type: 'info',
                        message: 'Produto apagado'
                    });
                    const index = this.tableData.indexOf(row);
                    this.$store.dispatch('removeListaRefeicao', this.tableData[index]);
                    this.tableData.splice(index, 1);
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: 'Produto não apagado'
                    });
                });
            }
        }
    }
</script>
<style scoped>
    .inputs {
        width: 80%;
    }
    .step2 {
        display: flex;
        flex-direction: column;
        text-align: center;
    }
    .step2 > div {
        margin: 15px 0;
    }

    @media screen and (min-width: 769px) {
        .textarea {
            width: 100%;    
        }
    }
</style>


